body {
	padding: 0;
	margin: 0;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana,
		'sans-serif';
	// background-image: url(http://www.guillaumeazadian.com/wp-content/uploads/2016/04/bg-color-anim.gif) !important;
	// background-repeat: repeat !important;
	// background-size: cover !important;
	// background-position: 0% 0% !important;
	// background-attachment: scroll !important;
	// background-color: #ffffff !important;
}

.Header {
	animation: HeaderAnim 1s ease-in-out;
	z-index: 1;
}

.Project {
	animation: ProjectAnim 1s ease-in-out;
	cursor: pointer;
	transform: translateY(0);
	transition: transform 0.3s ease-out;
	&:hover {
		transform: translateY(-5px);
		transition: transform 0.3s ease-out;
	}
}

.Intro {
	animation: IntroAnim 2s ease-in-out;
}

@keyframes IntroAnim {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes HeaderAnim {
	0% {
		opacity: 0;
		transform: translateY(-10vw);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes ProjectAnim {
	0% {
		opacity: 0;
		transform: translateX(-100vw);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}
